<template><section><h1>SimplePolylineGeometry</h1>
<ul>
<li><code v-pre>vc-geometry-polyline-simple</code> 组件用于加载线段。</li>
<li>需要作为 <code v-pre>vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre>vc-primitive</code>。</li>
</ul>
<h2>Example</h2>
<h3>加载线段</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive :appearance="appearance" :geometryInstances.sync="geometryInstances">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-polyline-simple ref="polyline" :positions="positions"></vc-geometry-polyline-simple>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span> <span class="hljs-attr">:geometryInstances.sync</span>=<span class="hljs-string">"geometryInstances"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-instance-geometry</span> <span class="hljs-attr">:attributes</span>=<span class="hljs-string">"attributes"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">vc-geometry-polyline-simple</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"polyline"</span> <span class="hljs-attr">:positions</span>=<span class="hljs-string">"positions"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-polyline-simple</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">appearance</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">geometry</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">geometryInstances</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">positions</span>: [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">102.1</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">29.5</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">106.2</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">29.5</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">106.2</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">33.5</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">108.2</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">35.5</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">102.1</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">33.5</span> }
        ],
        <span class="hljs-attr">attributes</span>: <span class="hljs-literal">null</span>
      }
    },
    mounted() {
      <span class="hljs-keyword">this</span>.$refs.polyline.createPromise.then(<span class="hljs-function">(<span class="hljs-params">{ Cesium, viewer, cesiumObject }</span>) =&gt;</span> {
        <span class="hljs-keyword">const</span> boundingSphere = Cesium.BoundingSphere.fromPoints(cesiumObject._positions)
        viewer.scene.camera.flyToBoundingSphere(boundingSphere)
      })
    },
    <span class="hljs-attr">methods</span>: {
      ready({ Cesium, viewer }) {
        <span class="hljs-keyword">const</span> { PerInstanceColorAppearance, ColorGeometryInstanceAttribute } = Cesium
        <span class="hljs-keyword">this</span>.appearance = <span class="hljs-keyword">new</span> PerInstanceColorAppearance({
          <span class="hljs-attr">flat</span>: <span class="hljs-literal">true</span>
        })
        <span class="hljs-keyword">this</span>.attributes = {
          <span class="hljs-attr">color</span>: <span class="hljs-keyword">new</span> ColorGeometryInstanceAttribute(<span class="hljs-built_in">Math</span>.random(), <span class="hljs-built_in">Math</span>.random(), <span class="hljs-built_in">Math</span>.random())
        }
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre>required</code> 指定线段位置信息。 <strong>结构: [{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>colors</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定每个顶点或每个线段的颜色数组。</td>
</tr>
<tr>
<td>colorsPerVertex</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定颜色数组是根据线段数取均值还是通过线段顶点插值。</td>
</tr>
<tr>
<td>arcType</td>
<td>Number</td>
<td><code v-pre>1</code></td>
<td><code v-pre>optional</code> 指定线条类型。 <strong>NONE: 0, GEODESIC: 1, RHUMB: 2</strong></td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定每个经纬度之间的采样粒度。 arcType 不是 ArcType.NONE 时有效。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定参考椭球体。</td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/SimplePolylineGeometry.html">SimplePolylineGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometry: null,
          geometryInstances: null,
          positions: [
            { lng: 102.1, lat: 29.5 },
            { lng: 106.2, lat: 29.5 },
            { lng: 106.2, lat: 33.5 },
            { lng: 108.2, lat: 35.5 },
            { lng: 102.1, lat: 33.5 }
          ],
          attributes: null
        }
      },
      mounted() {
        this.$refs.polyline.createPromise.then(({ Cesium, viewer, cesiumObject }) => {
          const boundingSphere = Cesium.BoundingSphere.fromPoints(cesiumObject._positions)
          viewer.scene.camera.flyToBoundingSphere(boundingSphere)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          const { PerInstanceColorAppearance, ColorGeometryInstanceAttribute } = Cesium
          this.appearance = new PerInstanceColorAppearance({
            flat: true
          })
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random())
          }
        }
      }
    }
  </script>